<template>
  <div v-if="topic" class="topic">
    <panel-container>
      <template #content>
        <div class="topic-content">
          <h2>{{ topic.title }}</h2>
          <p>
            <span>{{ topic.author.loginname }}</span>
            <template v-if="userInfo">
              <el-button
                @click="collect"
                v-if="!topic.is_collect"
                type="success"
                >收藏</el-button
              >
              <el-button @click="deCollect" v-else type="default"
                >取消收藏</el-button
              >
            </template>
          </p>
          <hr />
          <div class="topic-html" v-html="topic.content"></div>
        </div>
      </template>
    </panel-container>
    <!-- 评论列表 -->
    <panel-container v-if="topic.reply_count" class="discuss">
      <template #head>{{ topic.reply_count }}回复</template>
      <template #content>
        <!-- 评论列表 -->
        <replies-item
          v-for="(reply, index) in topic.replies"
          :key="reply.id"
          :index="index"
          :reply="reply"
          :mostReplyUpsId="mostReplyUpsId"
          :userInfo="userInfo"
          @getMyTopic="getMyTopic"
        />
      </template>
    </panel-container>
    <!-- 评论框 -->
    <panel-container>
      <template #head>添加回复</template>
      <template #content>
        <!-- 输入评论 -->
        <el-input type="textarea" v-model.trim="replyText"></el-input>
        <el-button @click="addReply">提交</el-button>
      </template>
    </panel-container>
  </div>
</template>

<script>
import PanelContainer from "@/components/PanelContainer.vue";
import {
  getTopic,
  collectTopic,
  deCollectTopic,
  addTopicReply,
} from "@/http/api";
import RepliesItem from "@/components/RepliesItem.vue";
import { mapState } from "vuex";
export default {
  components: { PanelContainer, RepliesItem },
  props: ["id"],
  data() {
    return {
      topic: null,
      replyText: "",
    };
  },
  computed: {
    ...mapState(["userInfo"]),
    mostReplyUpsId() {
      let id = "";
      let num = 0;
      this.topic.replies.forEach((reply) => {
        if (reply.ups.length > num) {
          id = reply.id;
          num = reply.ups.length;
        }
      });
      return id;
    },
    accesstoken() {
      const accesstoken = localStorage.getItem("accesstoken");
      return accesstoken || "";
    },
  },
  async created() {
    /* let params = { id: this.id };
    if (this.accesstoken) {
      params.accesstoken = this.accesstoken;
    }
    const res = await getTopic(params);
    this.topic = res.data; */
    this.getMyTopic();
  },
  methods: {
    // 收藏
    async collect() {
      const { topic, accesstoken } = this;
      console.log(accesstoken);
      await collectTopic({ topic_id: topic.id, accesstoken });
      this.topic.is_collect = true;
    },
    // 取消收藏
    async deCollect() {
      const { topic, accesstoken } = this;
      await deCollectTopic({ topic_id: topic.id, accesstoken });
      this.topic.is_collect = false;
    },
    // 获取文章详情
    async getMyTopic() {
      let params = { id: this.id };
      if (this.accesstoken) {
        params.accesstoken = this.accesstoken;
      }
      const res = await getTopic(params);
      this.topic = res.data;
    },
    // 添加评论
    async addReply() {
      const { replyText } = this;
      if (replyText) {
        // 添加评论
        await addTopicReply({
          accesstoken: this.accesstoken,
          content: this.replyText,
          id: this.topic.id,
        });
        this.replyText = "";
        this.getMyTopic();
      }
    },
  },
};
</script>

<style lang="less" scoped>
.topic {
  min-height: 100vh;
  .topic-content {
    padding: 20px 20px;
    /deep/ .topic-html {
      img {
        width: 100%;
      }
    }
  }
}
</style>