<template>
  <div>
    <div v-if="user">
      <panel-container>
        <template #head>首页</template>
        <template #content>
          <el-avatar
            shape="square"
            :size="50"
            :src="user.avatar_url"
          ></el-avatar>
          <span>{{ user.loginname }}</span
          ><br />
          <span>{{ user.score }}积分</span>
        </template>
      </panel-container>
      <panel-container>
        <template #head>最近创建的话题</template>
        <template #content>
          <topics-item
            v-for="index in 5"
            :topic="user.recent_topics[index]"
            :key="user.recent_topics[index].author.username"
          />
        </template>
      </panel-container>
      <panel-container>
        <template #head>最近参与的话题</template>
        <template #content>
          <topics-item
            v-for="topic in user.recent_replies"
            :topic="topic"
            :key="topic.author.username"
          />
        </template>
      </panel-container>
    </div>
    <div v-else>waiting</div>
  </div>
</template>

<script>
import { getUserDetail } from "@/http/api";
import PanelContainer from "@/components/PanelContainer.vue";
import TopicsItem from "@/components/TopicsItem.vue";
export default {
  components: { PanelContainer, TopicsItem },
  props: ["loginname"],
  data() {
    return {
      user: null,
    };
  },
  watch: {
    loginname: {
      async handler(loginname) {
        const res = await getUserDetail({ loginname });
        this.user = res.data;
      },
      immediate: true,
    },
  },
};
</script>

