// 统一管理接口
import http from "./http";
// 获取文章列表
export const getTopics = ({ params }) => http({
    url: '/topics',
    method: 'get',
    params,
})

// 获取文章详情
export const getTopic = ({ id, accesstoken }) => http({
    url: '/topic/' + id,
    method: 'get',
    data: { accesstoken }
})

// 验证 accesstoken 其实就是登录
export const login = (data) => http({
    url: '/accesstoken',
    method: 'post',
    data
})

// 收藏主题
export const collectTopic = (data) => http({
    url: '/topic_collect/collect',
    method: 'post',
    data
})

// 取消收藏主题
export const deCollectTopic = (data) => http({
    url: '/topic_collect/de_collect',
    method: 'post',
    data
})

// 评论点赞和取消点赞
export const replyLike = ({ reply_id, accesstoken }) => http({
    url: `/reply/${reply_id}/ups`,
    method: 'post',
    data: { accesstoken }
})

// 添加文章评论
export const addTopicReply = ({ reply_id, accesstoken, content }) => http({
    url: `/topic/${reply_id}/replies`,
    method: 'post',
    data: { accesstoken, content }
})

// 获取用户详情
export const getUserDetail = ({ loginname }) => http({
    url: `/user/${loginname}`,
    method: 'get',
})